import React from 'react';

import SampleImageOne from '../assets/images/PTFE_Architektur_08.jpg';
import SampleImageTwo from '../assets/images/Neubau_Schild.jpg';
import SampleImageThree from '../assets/images/Alt_PTFE_Nuenchritz_alt.jpg';

const slideRight = (el, time) => {

	let allItems = Array.from(el.children);
	
	allItems.map(item => {
		item.style.transition = `${time}ms all cubic-bezier(.45, .16, .5, 1)`;
	});
	
	let leftIncomingItem = allItems[0];
	let leftItem = allItems[1];
	let heroItem = allItems[2];
	let rightItem = allItems[3];
	let rightIncomingItem = allItems[4];

	heroItem.style.transform = `translateX(-44%) scale(.7) `;
	heroItem.style.opacity = .5;

	rightItem.style.transform = `translateX(-25%) scale(.9)`;
	rightItem.style.opacity = 1;
	rightItem.style.transition = `${time /2} all`;
	
	setTimeout(() => {
		rightItem.style.transform = `translateX(-100%) scale(1)`;
		rightItem.style.zIndex = 1;
	}, time / 1.8 );

	leftItem.style.transform = `translateX(28%) scale(.4)`;
	leftItem.style.opacity = 0;
	leftItem.style.zIndex = 0;
	
	let heroItemSrc = heroItem.style.backgroundImage;
	let rightItemSrc = rightItem.style.backgroundImage;
	let rightIncomingItemSrc = rightIncomingItem.style.backgroundImage;
	rightIncomingItem.style.transform = `translateX(-156%) scale(.7)`;
	rightIncomingItem.style.opacity = .5;

	setTimeout(() => {
		allItems.map(item => {
			item.style.transition = '';
		});
		[heroItem, rightItem, leftItem].map( item => {
			item.style.transform = '';
			item.style.opacity = '';
			item.style.zIndex = '';
		})
		rightIncomingItem.style.transform = '';
		rightIncomingItem.style.opacity = '';
		heroItem.style.backgroundImage = rightItemSrc;
		leftItem.style.backgroundImage = heroItemSrc;
		rightItem.style.backgroundImage = rightIncomingItemSrc;
	}, time * 1.7);
}

const ImageSlider = () => {

	const sliderHeroItem = React.useRef();
	const handleClick = (e) => {
		// INIT SLIDER
		slideRight(sliderHeroItem.current, 700);
		// PREVENT CLICKING DURING SLIDING ANIMATION
		let clickedButton = e.target
		clickedButton.setAttribute('disabled', true);
		setTimeout(() => {
			clickedButton.removeAttribute('disabled');
		}, 1200);
	}

	return (
		<div className="imageslider">
			<div className="imageslider-wrapper" ref={sliderHeroItem} >
				<div className="imageslider-item" style={{ backgroundImage: `url(${SampleImageOne})` }}></div>
				<div className="imageslider-item" style={{ backgroundImage: `url(${SampleImageTwo})` }}></div>
				<div className="imageslider-item" style={{ backgroundImage: `url(${SampleImageOne})` }}></div>
				<div className="imageslider-item" style={{ backgroundImage: `url(${SampleImageThree})` }}></div>
				<div className="imageslider-item" style={{ backgroundImage: `url(${SampleImageOne})` }}></div>
			</div>
			<div className="imageslider-nav">
				{/* <button className="imageslider-nav-left">&larr;</button> */}
				<button className="imageslider-nav-right" onClick={handleClick}>&rarr;</button>
			</div>
		</div>
	)
};

export default ImageSlider;