import React from 'react';
import TimelineItem from './TimelineItem';

const Timeline = () => {

	const years = React.useState([
		{
			id: 1965,
		},
		{
			id: 1991,
		},
		{
			id: 1994,
		},
		{
			id: 1996,
		},
		{
			id: 1997,
		},
		{
			id: 1998,
		},
		{
			id: 1999,
		},
		{
			id: 2001,
		},
		{
			id: 2005,
		},
		{
			id: 2010,
		},
		{
			id: 2014,
		},
		{
			id: 2015,
		},
		{
			id: 2017,
		},
		{
			id: 2018,
		},
	]);

	const [sliderState, setSliderState] = React.useState(0);
	const [handleState, setHandleState] = React.useState(0);
	const [gradientWidth, setGradientWidth] = React.useState(0);
	const [scrollGap, setScrollGap] = React.useState(0);
	const [wrapperPos, setWrapperPos] = React.useState(0);
	const [sliderTiming, setSliderTiming] = React.useState('1s all cubic-bezier(.64,.05,.4,.96)');

	const sliderRef = React.useRef();
	const gradientRef = React.useRef();
	const railRef = React.useRef();
	const wrapperRef = React.useRef();

	React.useEffect(() => {
		console.log(sliderRef)
		let sliderWrapper = sliderRef.current.parentElement.lastElementChild;
		console.log(sliderWrapper)
		let block = sliderWrapper.querySelector('.block-img');
		railRef.current.style.maxWidth = ((block.clientWidth + 10) * 2) + 'px';
		console.log(block.clientWidth);
		setScrollGap(parseInt((railRef.current.style.maxWidth)) / 15);
	}, []);

	React.useEffect(() => {
		setGradientWidth(() => {
			return (sliderState * (railRef.current.clientWidth / 100)) + ((parseInt(getComputedStyle(railRef.current).marginRight) + 10))
		});
	}, [sliderState]);

	React.useEffect(() => {
		console.log(scrollGap)
	}, [gradientWidth]);

	const animWrapper = (e) => {
		console.log(e.target.value)
	}

	const sliderSlower = (x, y) => {
		if (x > 80 ) {
			y = 30;
		} else if ( x > 50 ){
			y = 30;
		}
		return x * y;
	}
	
	return (
		<div className="timeline">

			<div className="timeline-slider" ref={sliderRef}>
				<div className="timeline-slider-gradient"
					ref={gradientRef}
					style={{ width: `${gradientWidth}px` }}>
				</div>
				<div className="timeline-slider-rail" ref={railRef}>
					<div className="timeline-slider-dates">
						{years[0].map(year => (
							<span key={year.id}>{year.id}</span>
						))}
					</div>
					<input
						onClick={(e) => {setSliderTiming('1s all cubic-bezier(.64,.05,.4,.96)') }}
						// onMouseDown={(e) => { setSliderTiming('0s') }}
						onChange={(e) => {
							// setSliderTiming('0s');
							setSliderState((e.target.value) / 5.1 );
							setHandleState((e.target.value));
							setWrapperPos(sliderSlower(e.target.value, 30));
							animWrapper(e);}}
						name="sliderChanger"
						value={handleState}
						type="range"
						min="0"
						max="500"
					/>
				</div>
			</div>

			<div className="timeline-wrapper" 
			ref={wrapperRef} style={{
				transform: `translateX(-${wrapperPos}px)`,
				transition: `${sliderTiming}`
			}}>
				{years[0].map((year, key) => (
					<TimelineItem key={key} year={year.id} />
				))}
			</div>

		</div>
	)
};

export default Timeline;
